.debug {

    [aria-hidden="false"] {
        
        &:before {
            content: 'aria-hidden=false';
            display: block;
            background: rgb(0, 255, 42);
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1000;
            padding: 10px 5px
        }
    }

    [aria-hidden="true"] {
        
        &:before {
            content: 'aria-hidden=true';
            display: block;
            background: rgb(255, 68, 0);
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1000;
            padding: 10px 5px
        }
    }
}
