//
//  Product Form
//  _____________________________________________

@use '../utils/_media-queries' as *;

.product-form {
    padding: 22px 0 38px;
    display: block;

    @include mobile { 
        padding: 22px 0 8px;
    }
    
    @include tablet-only { 
        padding: 22px 0 0px;
    }

    @include desktop {
        padding: 35px 0 0;
    }

    @include tablet {
        form {
            display: flex;
            flex-direction: column;
        }
    }

    &__controls {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-bottom: 25px;

        @include mobile { 
            background: white;
            position: fixed;
            bottom: 0;
            width: 100%;
            padding: 15px 15px 30px;
            left: 0;
            z-index: 5;
            flex-wrap: wrap;
        }

        @include tablet { 
            order: 3;
            padding-top: 20px;
        }

        @include desktop { 
            padding-top: 0px;
        }
    }

    &__title {
        width: 100%;

        @include tablet {
            display: none;
        }
    }

    &__price {
        @include tablet {
            display: none;
        }
    }

    &__price--compared {
        text-decoration-line: line-through;
        text-transform: uppercase;
        color: #767676;
    }

    &__actions {
        width: 100%;
        display: flex;

        @include mobile {
            padding-top: 15px;
        }
    }

    &__buttons {
        flex: 0 1 100%;
        margin-left: 19px;

        button {
            width: 100%;
        }
    }

    .button {
        width: 100%;
        display: block;

        &--add-to-cart-content {
            display: inline-block;
        }

        &--out-of-stock {
            display: none;
            
            &.active {
                display: inline-block;

                .icon-bag {
                    display: inline-block;
                }
                
                & + div {
                    display: none;
                }
            }
        }
    }

    &__quantity {
        max-width: 110px;
        width: 100%;

        .quantity-selector {
            max-height: 46px;
    
            &__quantity {
                padding: 11px 16px;
            }
        }
    }

    &__tags {
        padding: 0px 0 10px;

        @include tablet-only { 
            padding: 0 0 20px;
        }   
    }

    &__swatches {
        position: relative;
    }

    .label__content {
        font-size: 12px;
        line-height: 14px;
        text-transform: uppercase;
        padding: 9px;
        border: 1px solid var(--color-text);
        display: inline-block;
        margin-right: 3px;
        margin-bottom: 6px;
    }

    &__error-message {
        margin: 20px 0px;

        @include desktop {
            margin: 14px 0px;
        }
    }

    &__share-button {
        width: 100%;
        text-align: right;
        margin-top: -18px;

        .share-button__button {
            text-decoration: underline;
            text-align: right;
        }

        .share-button__details {
            font-size: 14px;
            line-height: 14px;
        }

        .field__input {
            max-width: 100%;
        }
    }
}
