@use '../utils/_media-queries' as *;

.mini-cart-container {
    .minicart-overlay {
        &.active {
            z-index: 101;
        }
    }
}

body.active-mini-cart {
    overflow: hidden;
}

.mini-cart {
    position: fixed;
    top: 0;
    right: -400px;
    width: 100%;
    max-width: 400px;
    height: 100%;
    z-index: 9999999999;
    background: var(--color-white);
    transition-timing-function: ease-in-out;
    transition-duration: .25s;
    transition-property: transform;
    will-change: transform;
    padding: 21px 35px 34px 35px;

    form {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    @include mobile {
        right: -320px;
        max-width: 320px;
        padding: 26px 30px 24px 15px;
    }

    &.active {
        transform: translateX(-100%);
    }

    &__header {
        display: flex;
        flex-direction: column;
        
        .icon-close,
        .close {
            margin-left: auto;
            cursor: pointer;
        }    
    }

    .close {
        color: var(--btn-background);
        padding: 5px;
        margin-right: -5px;
        line-height: 0;

        .icon-close,
        .icon-trash {
            stroke: var(--color-text);
            fill: var(--color-text);
        }

        svg {
            width: 18px;
            height: 18px;
        }
        
    }

    &__item-remove a {
        cursor: pointer;
    }

    &__items {
        flex-grow: 1;
        padding-top: 28px;
        overflow-y: auto;

        &::-webkit-scrollbar {
            width: 8px;
        }

        &::-webkit-scrollbar-thumb {
            background: #DADADA;
            border: 2px solid #FFFFFF;
            border-radius: 2px;
        }

        &::-webkit-scrollbar-thumb:hover {
            background: #767676;
        }
    }

    &__item {
        padding: 18px 0 22px;
        display: flex;
        flex-wrap: wrap;

        &:first-child {
            padding-top: 0;
        }

        &:last-child {
            padding-bottom: 0;
        }

        &-image-content {
            flex: 1 0 37%;
        }

        &-image {
            width: 100%;
        }

        &-details {
            flex: 1 0 61%;
            padding: 0 5px 0px 24px;
            display: flex;
            flex-direction: column;
            font-size: 12px;
            line-height: 12px;
        }

        &-name {
            margin-bottom: 6px;
            font-size: 14px;
            line-height: 20px;
            display: block;
            text-decoration: none;
        }

        &-options {
            color: var(--price-compare);
            padding-bottom: 7px; 
        }

        &-price {
            font-weight: bold;
            padding-bottom: 7px;
        }

        &-qty {
            max-width: 110px;
            margin: auto 0px 5px;
        }

        &-remove {
            button {
                font-size: 12px;
                line-height: 14px;
                color: var(--price-compare);
                padding: 0px;
                margin-top: 12px;
            }
        }
    }

    &__footer {
        padding-top: 24px;
        font-size: 14px;
        line-height: 14px;

        &-subtotal {
            font-size: 20px;
            line-height: 26px;
            padding-bottom: 19px;
        }

        &-info {
            display: flex;

            &--right {
                flex: 0 0 50%;
                text-align: end;
            }

            &--left {
                flex: 0 0 50%;
                text-align: start;
            }

            a {
                font-size: 1em;

                @include mobile {
                    font-size: 0.85em;
                }

            }
        }

        .btn {
            width: 100%;
            display: inline-flex;
            justify-content: center;
            margin-bottom: 19px;
        }
        
    }

    &__toggle {
        text-decoration: none !important;
        position: relative;

        &.start_animation {
            .mini-cart__count {
                height: 30px;
                width: 82px;
                background: var(--btn-secondary-background);
            }
        }
    }

    &__count {
        background: var(--color-text);
        border-radius: 50px;
        color: white;
        text-align: center;
        padding-top: 2px;
        right: 0;
        bottom: 0;
        display: flex;
        width: 22px;
        height: 22px;
        padding: 0;
        font-size: 12px;
        line-height: 12px;
        justify-content: center;
        align-items: center;
        -webkit-transition: all .3s ease;
        -moz-transition: all .3s ease;
        -ms-transition: all .3s ease;
        -o-transition: all .3s ease;
        transition: all .3s ease;

        &--large {
            width: 22px;
            height: 22px;
        }

        &--extra-large {
            font-size: 8px;
        }
    }

    &__error-message {
        display: none;
        text-align: center;
        padding: 16px 18px 10px 18px;
        font-style: italic;
        font-size: 12px;
        line-height: 16px;

        &.active {
            display: block;
        }
    }

    &__product-added-message {
        font-size: 32px;
        line-height: 40px;
        display: none;

        &.active {
            display: block;
            padding: 18px 0 0;
        }

        @media (max-width: 599px) { 
            font-size: 30px;
            line-height: 40px;
        }
    }

    &__free-shipping-bar {
        padding-top: 20px;

        .progress-bar .fspb-container {
            margin-bottom: 0 !important;
        }

        @include mobile {
            padding-top: 24px;
        }
    }
}
