//
//  Instant search
//  _____________________________________________

@use '../utils/_media-queries' as *;

.instant-search {

    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    max-height: calc(100vh - 120px);
    background-color: #FFF;
    font-size: 14px;
    line-height: 14px;
    z-index: 1000;
    overflow-y: auto;
    margin-top: -1px;
    border: none;
    padding: 40px 26px 26px;
    display: none;

    @include mobile {
        max-height: calc(100vh - 80px);;
        top: 84px;
        padding: 0 26px 26px;
        left: -15px;
        width: 100vw;
    }

    &.active {
        display: block;
    }

    &-dictionary {

        margin-bottom: 32px;

        &__heading {
            margin-bottom: 18px;
            font-weight: bold;
        }

        &__results {
            list-style-type: none;
            padding: 0;
            margin: 0;
        }

        &__item {
            margin-bottom: 11px;
        }

        &__item-heading {
            font-size: 16px;
            line-height: 16px;
            color: #767676;
        }

        &__item-link {
            text-decoration: none;
        }

    }

    &-suggestions {

        &__heading {
            margin-bottom: 25px;
            font-weight: bold;
        }

        &__wrapper {
            &:not(:first-of-type) {
                margin-top: 40px;
            }
        }

        &__results {
            list-style-type: none;
            padding: 0;
            margin: 0;
        }

        &__item {
            margin-bottom: 20px;
        }

        &__item-link {
            display: flex;
            text-decoration: none;
        }

        &__item-image {
            width: 100%;
            display: block;
        }

        &__item-image-wrapper {
            width: 30%;
            max-width: 100px;
        }

        &__item-text-wrapper {
            width: 70%;
            padding-left: 14px;
        }
        
        &__brand,
        &__price,
        &__item-heading {
            font-size: 14px;
            line-height: 20px;
            margin-bottom: 6px;
        }

        &__item-heading:hover {
            text-decoration: underline; 
        }

        &__brand {
            text-transform: uppercase;
        }

        &__old-price{
            text-decoration-line: line-through;
            text-transform: uppercase;
            color: #767676;
            margin-left: 4px;
        }

        &__view-all {
            text-decoration: none;
            display: inline-block;
            background: 0;
            color: var(--btn-background);
            margin: 14px 0 10px;
            padding: 0;
            font-weight: normal;

            span {
                border-bottom: 1px solid;
            }

            svg {
                width: 20px;
                height: 20px;
                margin-left: 14px;
                fill: var(--btn-background);
                stroke: var(--btn-background);
                transform: none;
                position: relative;
                top: 5px;
            }

            &:hover {
                outline: none;
            }
        }

    }

    &-events{

        &:before {
            content: '';
            display: block;
            height: 1px;
            margin: 16px 0;
            background-color: var(--color-border);
        }

        &__link {
            text-decoration: none;
            text-transform: uppercase;
        }

    }
   
    &__no-result {
        margin: 0;
    }

    @include desktop {
        border: 1px solid var(--color-border);
        border-top: none;
    }

}