//
//  Quantity Selector
//  _____________________________________________

.quantity-selector {
	position: relative;
    max-height: 40px;

	label {
		display: none;
	}

	button {
		background: transparent;
		display: inline-block;
		color: var(--color-text);

		.icon {
			fill: var(--color-text);
		}
	}

	&__quantity {
		padding: 8px 16px ;
		text-align: center;
		max-width: 100%;
		-moz-appearance: textfield; /* Firefox */

		/* Chrome, Safari, Edge, Opera */
		&::-webkit-outer-spin-button,
		&::-webkit-inner-spin-button {
			-webkit-appearance: none;
			margin: 0;
		}
	}

	&__minus,
	&__plus {
		display: flex;
    	align-items: center;
		position: absolute;
		top: 0;
		line-height: 0;
		height: 100%;
		width: 40%;
		cursor: pointer;
        padding: 0;
	}

	&__plus {
		right: 0;
	}

	&__minus {
		left: 0;
	}
}
