//
//  Product Single
//  _____________________________________________

@use '../utils/_media-queries' as *;

.product-single {
    padding-bottom: 85px;

    @include tablet {
        display: flex;
        justify-content: flex-end;
        align-items: flex-start;
    }

    &__media {
        
        @include tablet {
            width: 50%;
            top: 120px;
            position: sticky;
            padding-top: 15px;
        }

        @include desktop {
            width: calc(100% - 405px);
        }
    }

    &__details {
        @include tablet {
            padding-top: 28px;
            width: 50%;
            padding-left: 57px;
        }

        @include desktop {
            width: 405px;
            padding-right: 48px;
        }
    }

    &__details-wrapper {

        @include tablet {
            max-width: 310px;
        }
    }

    &__title {
        padding: 15px 0 8px;
        margin-bottom: 0px;

        @include mobile { 
            display: none;
        }

        @include tablet-only { 
            padding: 0px;
            font-size: 25px;
        }

        @include desktop {
            font-size: 32px;
            line-height: 40px;
        }
    }

    &__price {

        @include mobile { 
            display: none;
        }

        @include desktop {
            width: 100%;
            font-size: 20px;
        }

        &--compared {
            text-decoration-line: line-through;
            padding-left: 7px;
            color: var(--price-compare);
        }

        &--current {
            &:hover {
                color: var(--btn-secondary-background);
            }
        }
    }

    &__share {
        width: 100%;
        font-size: 12px;

        &:hover {
            text-decoration: underline;
        }

        @include tablet {
            display: none;
            margin: 0;
        }

        .share-button__button {
            font-size: 14px;
            text-decoration: underline;
            text-align: right;
        }

        &--top { }

        &--footer {
            margin: 0;

            .share-button__button {
                font-size: 12px;
                text-align: left;
                text-decoration: none;
            }

            @include tablet {
                display: block;
                padding-bottom: 36px;
            }
        }
    }

    &__description {
        font-size: 14px;
        line-height: 20px;
        padding-bottom: 20px;

        @include tablet {
            position: relative;
            padding: 0px;

                &.hidden {
                    &::before {
                        display: block;
                    }
                }

            &-btn {
                position: absolute;
                bottom: -10px;
                right: 5px; 
                padding: 1px;
                background-color: var(--color-white);
                color: var(--color-text);
                font-size: 12px;
                line-height: 14px;
                font-weight: normal;
                text-decoration: underline;
                z-index: 2;
                display: block;
                width: auto;
                margin: 0px;
            }

            &.active {
                
                .product-single__description-text {
                    transition: max-height 0.55s ease-in;

                    &::before {
                        display: none;
                    }
                }

                .product-single__description-btn {
                    bottom: -22px;
                    font-size: 0px;

                    &::before {
                        content: "See less";
                        font-size: 12px;
                        text-decoration: underline;
                    }
                }
            }

            &--fully-visible {
                button {
                    display: none;
                }
                .product-single__description-text:before {
                    background: transparent;
                }
            }
        }

        &-text {
            display: block;

            @include tablet {
                max-height: 55px;
                overflow: hidden;
                transition: max-height 0.55s ease-out;

                &::before {
                    content: " ";
                    height: 55px;
                    width: 100%;
                    display: block;
                    position: absolute;
                    bottom: 0px;
                    background: linear-gradient(
                    180deg
                    ,rgba(255,255,255,0) 0,#fff 100%);
                }
            }
        } 
    }

    .accordion {
        border-top: none;
        padding-bottom: 22px;

        @include tablet {
            padding-bottom: 10px;
        }
    }

    @include desktop {
        &--half {
            position: relative;
            width: 100vw;
            left: calc(-50vw - -50%);
            .product-single__media,
            .product-single__details {
                width: 50%;
            } 
        }

        &--floating {
            .product-single__details {
                position: sticky;
                top: 98px;
            }
            .product-single__details-wrapper{
                max-width: 480px;
            }
            .product-single__media {
                padding-top: 36px;
            }
            .swiper-gallery-pagination {
                display: none !important;
            } 
            .gallery__thumbnails {
                display: none;
            }
            .gallery {
                max-width: none;
                display: block;
            }
            .swiper-wrapper {
                display: flex;
                flex-direction: column;
                height: auto !important;
            }
            .gallery__item {
                margin-bottom: 18px;
            }
        }
    }

    @include desktop-large {
        &--floating {
            .product-single__details {
                padding-left: 133px;
                padding-right: 106px;
            }
        }
    }
}
