@use '../utils/_media-queries' as *;

//
//  Pagination
//  _____________________________________________

.pagination {
    a {
        text-decoration: none;
    }

	&__itens {
		display: flex;
		align-items: center;
		justify-content: center;
		list-style-type: none;

        @include tablet {
            display: block;
        }
	}

    .page a ,
    .page.current ,
    .deco {
        border: 1px solid var(--color-border);
        padding: 9px 13px;
        font-size: 14px;
        line-height: 26px;
        margin-right: 9px;
        vertical-align: middle;

        @include tablet {
            padding: 10px 15px;
            margin-right: 3px;
        }
    }

    .page a:hover {
        border: 1px solid var(--color-text);
    }

    .page.current {
        border: 2px solid var(--color-text);
        padding: 2px 13px;

        @include tablet {
            padding: 10px 15px;
        }
    } 

    .deco {
        padding: 3px 12px;

        @include tablet {
            padding: 10px 11px;
        }
    }

    .icon-arrow {
        vertical-align: bottom;
        width: 22px;
        height: 22px;

        @include tablet {
            vertical-align: middle; 
            width: 30px;
            height: 30px;
        }
    }

    &__arrow-right {
        margin: 0 0 0 3px;

        @include tablet {
            margin: 0 0 0 10px;
        }
    }

    &__arrow-left {
        margin: 0 3px 0 0;

        @include tablet {
            margin: 0 10px 0 0;
        }

        .icon-arrow {
            transform: rotate(180deg);
        }
    }

    .next , .prev {
        display: none;
    }
}
