
//
//  Filters
//  _____________________________________________
@use '../utils/_media-queries' as *;

.filters {
    position: fixed;
    top: 0;
    right: -375px;
    width: 100%;
    max-width: 375px;
    height: 100%;
    z-index: 9999999999;
    background: var(--color-white);
    display: flex;
    flex-direction: column;
    padding: 33px 0 25px;
    border-left: 1px solid #f7f7f7;

    transition-timing-function: ease-in-out;
    transition-duration: .25s;
    transition-property: transform;
    will-change: transform;

    @include desktop {
        padding: 42px 0 34px;
    }

    &.hidden {
        display: none;
    }

    &.active {
        transform: translateX(-100%);
    }

    &-container {
        .global-overlay {
            &.active {
                z-index: 101;
            }
        }
    }

    &__close {
        position: absolute;
        top: 0;
        right: 0;
        margin-top: 38px;
        margin-right: 15px;
        padding: 5px;

        color: #767676;
        background: 0 0;
        line-height: 0;

        svg {
            fill: var(--btn-background);
            stroke: var(--btn-background);
            width: 18px;
            height: 18px;
        }
    }

    &__heading {
        font-size: 16px;
        line-height: 16px;
        margin-bottom: 32px;

        .count-bubble {

            &[data-filter-count='0'] {
                display: none;
            }

            &:before {
                content: '(' attr(data-filter-count) ')'; 
                display: inline-block;
            }
        }

        @include desktop {
            margin-bottom: 34px;
        }
        
    }

    &__applied {
        padding: 0 6px;
        font-size: 14px;
        line-height: 12px;
        margin: 4px 0 36px 0;
        font-weight: bold;
        font-family: 'Circular Std';
        font-weight: 700;
        
        @include desktop {
            margin: 2px 0 39px 0;
        }
    }

    &__clear-all {
        color: #767676 !important;
        text-decoration: none;
        margin-left: 12px;
        font-size: 14px;
        font-weight: normal;
        border-bottom: 1px solid;
        padding: 0;
        background: 0 0;

        &:active {
            text-decoration: none;
        }
    }

    &__items {
        margin-top: 5px;
        height: calc(100vh - 197px);
        overflow-y: auto;
    }

    &__item-heading {
        font-weight: 700;
        font-size: 16px;
        line-height: 32px;
        margin: 0;
        margin-bottom: 10px;
    }

    &__selected-filters {
        margin-right: auto;
        font-size: 16px;
        line-height: 16px;
        font-weight: 400;
        color: #767676;
        white-space: nowrap;
        width: 50%;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    &__value {
        margin-left: 9px;
        margin-right: auto;
        text-transform: uppercase;
        color: #767676;
    }

    &__text-options {
        margin: 5px 0 26px;
        display: flex;
        flex-wrap: wrap;
        padding: 0;
        list-style-type: none;
    }

    &__text-option-item {
        margin: 5px;
        position: relative;

        input[type='checkbox'] {
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            position: absolute;
            z-index: 2;
            opacity: 0;
            width: 100%;
            height: 48px;
            margin: 0;

            &:focus-visible {
                & + label {
                    outline: 2px solid var(--color-focus);
                }
            }

            & + label {
                font-size: 16px;
                line-height: 16px;
                background-color: var(--color-white);
                padding: 16px 8px;
                box-shadow: 0 0 0 1px #c8c8c8;
                margin: 0;
                top: 0!important;
                cursor: pointer;
                text-transform: uppercase;
                min-width: 48px;
                text-align: center;
            }

            &:checked + label {
                box-shadow: 0 0 0 2px var(--color-text);
            }

            &:hover {

                & + label {
                    box-shadow: 0 0 0 1px var(--color-text);
                }
            }
        }
    }

    &__list-options {
        padding: 0;
        margin: 5px 0 26px;
        list-style-type: none;
    }

    &__list-option-item {
        padding: 0;
        margin: 5px;
        position: relative;

        a {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: 0;
        }

        &-header {
            display: flex;
            text-decoration: none;
            justify-content: space-between;
        }

        &.selected {
            font-weight: bold;
        }

        label {
            cursor: pointer;

            &:hover {
                text-decoration: underline;
            }
        }

        &-checkbox {
            display: grid;
            grid-template-columns: 20px minmax(100px, 1fr);
            column-gap: 15px;
            margin: 0 0 12px;

            input[type=checkbox]+label {
                top: 0;
                margin: 6px 0 0 0;
                text-decoration: none !important;
                text-underline-offset: 2px;
            }
        }
    }

    &__checkbox-option-label {
        width: calc(100% - 36px);
        cursor: pointer;
        span {
            float: right;
            margin-top: -1px;
        }
    }

    &__header,
    &__items {
        button:hover {
            outline: none;
            outline-offset: 0;
        }
    }

    &__header,
    &__items,
    &__footer {
        padding: 0 25px;
    }

    &__footer {
        margin-top: auto;
        padding-top: 16px;
        .btn {
            height: 52px;
        }
    }

    .accordion {
        border: none;
        margin-top: 5px;
        &__item {
            &.active {
                .icon {
                    transform: rotate( 180deg) scale(1.5);
                }
            }
        }
    }

    .sort-by-cards {

        &.filters__text-options {
            display: flex;
            flex-direction: column;
        }

        .filters__text-option-item {
            margin: 5px 5px 10px;

            span {
                height: 32px;
                display: block;
                position: relative;
            }

            input[type=checkbox] {
                height: 100%;
            }

            input[type=checkbox]+label {
                display: block;
                font-size: 13px;
                line-height: 16px;
                text-transform: none;
                padding: 8px;
            }
        }
    }
        
    .noUi-horizontal {
        height: 9px;
        margin: 10px 10px 30px;

        .noUi-handle {
            top: -6px;
            right: -15px;
            width: 22px;
            height: 22px;
            border-radius: 50%;
            box-shadow: unset;
            cursor: pointer;
            transition: transform .1s;
            
            &:before, &:after {
              content: none
            }
            &:focus{
                box-shadow: var(--focus-visible);
            }
        }
    }

    .noUi-connect {
        background: var(--color-text);
    }

    .price-range {
        display: grid;
        grid-gap: 5px 20px;
        grid-template-columns: 1fr 1fr;

        input {
            padding: 17px 30px 15px 37px;
        }
    }

    .price-range-slider {
        grid-column: span 2;
    }
}