@use '../utils/_media-queries' as *;

//
//  Collection
//  _____________________________________________

.collection {

    &__title {
        text-align: center;
        padding-top: 50px;
    }

    &__image {

        .banner-overlay {
            margin: 0;
        }
    }

    .breadcrumbs {
        margin: 30px 0 15px;

        @include tablet {
            margin: 41px 0 25px;
            text-transform: none;
            position: sticky;
            top: 95px;
            z-index: 10;
            background: var(--color-white);
            padding-bottom: 10px;
        }
    }

    &__description {
        
        @include tablet {
            width: 73%;
        }

        @include desktop {
            width: 44%;
        }
    }

    .product-grid {
        padding: 54px 0;
    }

    &__footer {
        display: flex;
        justify-content: flex-end;
        padding-bottom: 80px;

        @include tablet {
            justify-content: space-between;
            align-items: center;
        }

        .breadcrumbs {
            position: inherit;
            z-index: 1;
            background: transparent;
            padding-bottom: 0;
        }
    }

    &__filters {
        z-index: 11;

        &--hidden{
            display: none !important;
        }
        
        @include mobile {
            max-width: 350px;
            margin: auto;
            border: 2px solid var(--color-border);
            position: fixed;
            bottom: 5vw;
            left: 0;
            right: 0;
        }
        
        @include tablet {
            text-align: end;
            position: sticky;
            top: 100px;
            width: max-content;
            margin-left: auto;
            margin-bottom: -65px;

            &--description {
                top: 109px !important;
                margin-top: -29px !important;
            }
        }

        @media (max-width: 355px) { 
            max-width: 290px;
        }

        &-button {
            font-size: 14px;
            text-align: center;
            color: var(--color-text);
            background: var(--color-white);
            width: 100%;
            padding: 15px;

            @include tablet {
                padding: 0;
                background: transparent;
                position: relative;

                &--description{
                    bottom: 11px !important;
                }
            }
            
            &:hover {
                outline: none;
                background: var(--color-white);
                text-decoration: underline;
            }
            
            .count-bubble {
    
                &[data-filter-count='0'] {
                    display: none;
                }
    
                &:before {
                    content: '(' attr(data-filter-count) ')'; 
                    display: inline-block;
                }
            }

        }

        &-product-count {
            margin: 0;

            @include tablet {
                float: left;
            }
        }

    }

}
