//
//  Search bar
//  _____________________________________________

.search-bar {

    &__form {
        display: flex;
        align-items: center;
        position: relative;
    }

    &__submit-wrapper {
        position: absolute;
        left: 15px;

        .icon-search {
            width: 32px;
            height: 32px;
            transform: scale(1);
        }
    }

    &__submit {
        padding: 0;
        margin: 0;
        display: block;
    }

    &__input-wrapper {
        width: 100%;
        background: white;
    }

    &__input {
        max-width: 100%;
        margin: 0;
        padding-left: 59px;
        padding-right: 50px;
        font-weight: bold;

        &[type="search"]::-webkit-search-decoration,
        &[type="search"]::-webkit-search-cancel-button,
        &[type="search"]::-webkit-search-results-button,
        &[type="search"]::-webkit-search-results-decoration {
            display: none;
        }

    }

    &__actions {
        position: absolute;
        right: 15px;
    }

    &__close {
        padding: 0;
        margin: 0;
        display: none;
    }
}
