//
//  Accordions
//  _____________________________________________

.accordion {
    border-top: 1px solid var(--color-border);
    padding-bottom: 17px;
    
    &__item {
        .icon {
            width: 22px;
            height: 22px;
            align-self: end;
            fill: var(--color-text);
        }

        .icon-less {
            display: none;
        }
    
        &.active {
            transition: max-height 0.55s ease-in;
    
            .icon-add {
                display: none;
            }

            .icon-less {
                display: block;
            }

            .accordion__content {
                transition: max-height 0.35s ease-in;
            }
        }
    }

    &__heading {
        padding: 0px;
        margin: 17px 0 15px;
        width: 100%;
        text-align: left;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-weight: normal;
        font-size: 14px;
        line-height: 22px;
        background: var(--color-white);
        color: var(--color-text);

        &:hover {
            outline: none;

            .icon {
                fill: var(--color-text);
            } 
        }

        &:active {
            text-decoration: none;
        }
    }

    &__content {
        font-size: 14px;
        overflow-y: hidden;
        max-height: 0;
        transition: max-height 0.35s ease-out;
    }

 //
//  Accordion with black background
//  _____________________________________________

    &--white {
        color: var(--color-white);
        border-top: none;
        padding: 13px 0px;

        .accordion__heading {
            background: var(--btn-background);
            color: var(--color-white);
            font-size: 16px;
            line-height: 22px;
            font-weight: bold;

            .icon {
                fill: var(--color-white);
            }

            &:hover {
                fill: var(--color-white);
            }
        }
    }

 //
//  Accordion inverted
//  _____________________________________________

    &--inverted {
        padding: 8px 0px;

        .icon {
            width: 10px;
            height: 10px;
            margin-right: 11px;
            align-self: center;
        }

        .accordion__heading {
            padding: 8px 0px;
            justify-content: flex-start;
            font-size: 12px;
            line-height: 14px;
            margin: 0px;
        }

        .accordion__content {
            font-size: 12px;
            line-height: 14px;
        }
    } 
}
