@use '../utils/_media-queries' as *;

body.quick-add-to-cart-active {
    overflow: hidden;
}

.quick-add-to-cart {
    display: block;

    &__overlay.global-overlay {
        z-index: 102;
    }

    &__container {
        position: fixed;
        left: 0;
        bottom: -312px;
        width: 100%;
        height: 312px;
        z-index: 999999999;
        background: var(--color-white);
        transition-timing-function: ease-in-out;
        transition-duration: .25s;
        transition-property: transform;
        will-change: transform;
        transform: translateX(0);
    }

    &.active {
        .quick-add-to-cart__container {
            transform: translateY(-100%);
        }
    }

    &.hidden {
        .quick-add-to-cart__container {
            display: none;
        }
    }

    &__close {
        background: transparent;
        position: absolute;
        z-index: 100;
        line-height: 0;
        top: 17.1px;
        right: 21.7px;

        .icon-close {
            stroke: #767676;
            fill: #767676;
            width: 15px;
            height: 15px;
        }
    }

    &__content {
        background-color: var(--color-white);
        height: 100%;
        width: 100%;
        overflow: auto;
        opacity: 0;
        transition: 0.5s ease-in-out opacity;
        padding: 43px 22px 16px 22px;

        .active & {
            opacity: 1;
        }
    }

    &__div-header {
        display: flex;
        width: 100%;
        margin-bottom: 27px;
    }

    &__image-div {
        width: 83px;
    }

    &__text-container {
        padding-left: 20px;
    }

    &__name {
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 8px;
    }

    &__price {
        font-size: 14px;
        line-height: 14px;
        display: block;

        &-compared {
            text-decoration-line: line-through;
            padding-left: 8px;
            color: var(--price-compare);
        }
    }

    &__select-size {
        display: block;
        font-size: 14px;
        line-height: 14px;
        margin-bottom: 13px;
        color: var(--color-primary);
        text-transform: uppercase;
    }

    &__quantity,
    variant-selector,
    .product-addtocart__button,
    .swatches__swatch--text-Color,
    .swatches__swatch-label {
        display: none;
    }

    &__swatches {
        width: 100%;
    }

    &__swatches-div {
        display: flex;

        div {
            margin-right: 5px;
            text-align: center;
            width: 60px;
            height: 60px;
            border: 1px solid var(--color-border);
            display: table-cell;
            vertical-align: middle;
        }

        button {
            width: 100%;
            height: 100%;
            text-decoration: none !important;

            &:focus {
                border: 1px solid var(--color-text);
            }

            &[data-available="false"] {
                background: #e9e9e9 !important;
                opacity: 0.5;
                color: #767676 !important;
                text-align: center;
                position: relative;

                &::before {
                    bottom: 0;
                    left: 0;
                    position: absolute;
                    right: 0;
                    top: 0;
                    content: '';
                    opacity: 0.5;
                    background: rgb(255,255,255);
                    background: -moz-linear-gradient(to left top, rgba(255,255,255,0) 48%, rgba(232,232,225,1) 48%, rgba(232,232,225,1) 53%, rgba(255,255,255,0) 53%);
                    background: -webkit-linear-gradient(to left top, rgba(255,255,255,0) 48%, rgba(232,232,225,1) 48%, rgba(232,232,225,1) 53%, rgba(255,255,255,0) 53%);
                    background: linear-gradient(to left top, rgba(255,255,255,0) 48%, #a9a9a9 49%, #a9a9a9 51%, rgba(255,255,255,0) 51%);
                    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff",endColorstr="#ffffff",GradientType=1);
                }
                &::after {
                    bottom: 0;
                    left: 0;
                    position: absolute;
                    right: 0;
                    top: 0;
                    content: '';
                    opacity: 0.5;
                    background: rgb(255,255,255);
                    background: -moz-linear-gradient(to right top, rgba(255,255,255,0) 48%, rgba(232,232,225,1) 48%, rgba(232,232,225,1) 53%, rgba(255,255,255,0) 53%);
                    background: -webkit-linear-gradient(to right top, rgba(255,255,255,0) 48%, rgba(232,232,225,1) 48%, rgba(232,232,225,1) 53%, rgba(255,255,255,0) 53%);
                    background: linear-gradient(to right top, rgba(255,255,255,0) 48%, #a9a9a9 49%, #a9a9a9 51%, rgba(255,255,255,0) 51%);
                    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff",endColorstr="#ffffff",GradientType=1);
                }
            }
        }
    }

    &__start-button {
        margin-bottom: 15px;
        
        @include tablet {
            display: none;
        }
    }
}