//
//  Default Carousel
//  _____________________________________________

@use '../utils/_media-queries' as *;

.carousel {
    overflow: hidden;
    width: calc(100% + 30px);
    margin: 0 0 0 -15px;
    padding: 0 0 40px 0;

    @include tablet {
        width: calc(100% + 40px);
        margin: 0 0 0 -20px;
    }

    @include desktop {
        width: calc(100% + 140px);
        margin: 0 0 0 -70px;
        padding: 0 50px 0;
    }

    img {
        display: block;
    }

    &__wrapper {
        padding: 0;
    }

    &__heading-wrapper {
        text-align: center;
        display: block;
        width: 100%;
        padding: 25px 0 35px;

        @include desktop {
            padding: 50px 0 45px;
        }
    }

    &__heading {
        margin: 0;
    }

    &__item {
        width: 100%;
        padding: 0 20px;
        opacity: 1;
        transition: .4s ease-in-out opacity;

        /*@include desktop {

            &.is-selected {
                opacity: 1;
            }
    
            &:not(.is-selected) {
                opacity: 0;
            }
        }*/
    }

    &__item-container {
        border: 1px solid #C8C8C8;
    }

    // Loading mode

    &__loading {

        .carousel {
            opacity: 0;
            transition: 1s ease-in-out opacity;
        }

        &.active {

            .carousel {
                opacity: 1 !important;
            }
        }
    }

    // Hero mode

    &__hero {
        overflow: hidden;

        .carousel {
            width: 100%;
            padding: 0;
            margin: 0;
        }

        .carousel__item {
            opacity: 1 !important;
            padding: 0 !important;
        }
    }
}
