@use '../utils/_media-queries' as *;

body.quick-view-active {
    overflow: hidden;
}

.quick-view {
    display: block;

    &__container {
        position: fixed;
        top: 0;
        right: -300px;
        width: 300px;
        height: 100%;
        z-index: 999999999;
        background: var(--color-white);
        transition-timing-function: ease-in-out;
        transition-duration: .25s;
        transition-property: transform;
        will-change: transform;
        transform: translateX(0);

        @include tablet {
            right: -400px;
            width: 400px;
        }
    }

    &.active {

        .quick-view__container {
            transform: translateX(-100%);
        }
    }

    &.hidden {
        .quick-view__container {
            display: none;
        }
    }

    &.active-slider {
        .quick-view__content {
            overflow-y: auto;
        }
    }


    &__close {
        background: transparent;
        position: absolute;
        z-index: 100;
        line-height: 0;
        top: 21px;
        right: 15px;
        padding: 5px;

        .icon-close {
            stroke: #767676;
            fill: #767676;
            width: 18px;
            height: 18px;
        }
    }

    &__content {
        background-color: var(--color-white);
        height: 100%;
        width: 100%;
        overflow: auto;
        opacity: 0;
        transition: 0.5s ease-in-out opacity;

        .active.loaded & {
            opacity: 1;
        }
    }

    &__image-wrapper {
        min-height: 300px;
    }

    .quick-view__carousel {

    }

    .quick-view__carousel-item {
        width: 100%;
    }

    &__text-container {
        padding: 32px 30px 0;

        @include tablet {
            padding-top: 32px;
        }
    }

    &__name {
        font-size: 16px;
        line-height: 20px;

        @include tablet {
            font-size: 32px;
            line-height: 40px;
            letter-spacing: -1px;
        }
    }

    component-swatch {
        width: 100%;
    }

    variant-selector {
        display: none;
    }

    &__price {
        line-height: 14px;
        padding-top: 8px;

        &-current {
            padding-right: 7px;
            color: var(--color-primary);
        }

        &-compared {
            text-decoration-line: line-through;
            padding-left: 7px;
            color: var(--price-compare);
        }
    }

    &__buttons {
        flex: 0 1 100%;
        margin-top: 32px;
        margin-left: 19px;

        button {
            width: 100%;
        }
    }

    &__button {
        width: 100%;
        display: block;

        &-add-to-cart-content {
            display: inline-block;
        }

        &-out-of-stock {
            display: none;
            
            &.active {
                display: inline-block;

                .icon-bag {
                    display: inline-block;
                }
                
                & + div {
                    display: none;
                }
            }
        }
    }

    &__overlay.global-overlay {
        z-index: 101;
    }

    &__form {
        padding: 10px 30px 34px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    component-addtocart {
        width: 62%;

        .product-form__button {
            padding: 13px 0px 12px;
        }
    }

    &__quantity {
        width: 32%;

        .quantity-selector {
            max-height: 38px;
        }

        .quantity-selector__quantity {
            padding: 7px 16px;
        }
    }

    &__details {
        font-size: 14px;
        line-height: 14px;
        margin-top: 26px;
    }

    &__carousel {
        margin: 0;
        padding: 0;
        width: 100%;
    }

    .swatches__swatch--text-Color {
        display: none;
    }
}