@use '../utils/_media-queries' as *;

//
//  Footer
//  _____________________________________________

#shopify-section-footer::before {
	content: "";
	display: block;
	height: 60px;
    width: 100%;
    background-image: var(--footer-img);  
    margin-bottom: -21px;
	margin-top: 21px;
	
	@include tablet {
		margin-bottom: -2px;
	}
}

.footer {
	background-color: var(--footer-background);
	color: var(--color-white);

	.template-product & {

        @include mobile {
            padding-bottom: 230px;
        }
    }

	a {
		color: var(--color-white);
		text-decoration: none;

		&:hover {
			text-decoration: underline;
		}
	}

	&__container {
		max-width: 450px;

		@include tablet {    
			display: flex;
			justify-content: space-between;
			max-width: var(--container-max-width);
		} 
	}

	&__newsletter {
		flex: 0 0 45%;

		@include desktop {
			flex: 0 0 35%;
		}

		&-title {
			padding-bottom: 7px;

			@include tablet {
				padding-bottom: 23px;
				max-width: 390px;
			}
		}

		&-text {
			padding-bottom: 13px;

			@include tablet {
				max-width: 390px;
				padding-bottom: 18px;
			}
		}

		input {
			background: transparent;
			margin-bottom: 15px;
			padding: 22px 47px 20px 37px;
			color: var(--color-white);
			max-width: 100%;

			@include tablet {   
				padding: 15px 47px 14px 37px;
				max-width: 390px;
			}
		}

		&-btn {
			background: var(--color-white);
			color: var(--color-text);
			width: 100%;
			margin-bottom: 40px;
			padding: 24px 40px 21px;

			@include desktop {
				width: 146px;
				padding: 16px 34px 15px;
			}

			&:hover {
				background: var(--color-white);
			}
		}
	}

	.social-links {
		flex-direction: column;

		@include tablet {
			margin: 0 30px;
		}

		@include desktop-large {
			margin: 0 50px;
		}

		ul {
			@include mobile {
				display: flex;
				padding-left: 0px;
				justify-content: space-between;
				width: 100%;
			}
		}

		&__list {
			display: flex;
    		justify-content: space-between;
			margin-top: 13px;

			@include tablet {
				display: block;
				margin-top: 25px;
			}
		}

		&__link {
			color: var(--color-white);
		}

		.icon {
			fill: var(--color-white);
		}

		&__item {
			margin-bottom: 10px;

			span {
				margin: auto 0;
			}
		}

		&__title {
			padding-bottom: 20px;
			font-weight: 700; 

			@include tablet {
				padding-left: 40px;
			}

			@include desktop {
				padding-bottom: 25px;
			}

			& + ul {
				@include desktop {
					margin-block-start: 0em;
					margin-block-end: 0em;
					margin-top: 5px;
				}
			}
		}
	}

	&__link-list {
		flex: 0 0 22%;

		@include desktop-large {
			flex: 0 0 45%;
		}

		.accordion {
			@include desktop {
				display: flex;
			}

			&--white {
				@include tablet {
					padding: 0px;
					margin-top: -17px;
				}

				@include desktop {
					margin-top: 0px;
				}
			}

			&__heading {
				@include desktop {
					display: inline-block;
					pointer-events: none;
					padding: 0 0 25px;
					margin: 0px;

					svg {
						display: none;
					}
				}
			}

			&__item {
				@include desktop {
					margin-right: 40px;
				}

				@include desktop-large {
					margin-right: 77px;
				}
			}

			&__content {
				font-size: 16px;

				@include desktop {
					max-height: none;
					overflow: visible;
					visibility: visible;
				}

				&.hidden {
					visibility: hidden;
					display: block;

					@include desktop {
						visibility: visible;
					}
				}

				a {
					display: block;
					margin: 5px;

					&[href^=tel] {
						white-space: nowrap;
					}

					@include desktop {
						margin: 5px 0px;
					}
				}
			}
		}
	}

	&__footnote {
		font-size: 12px;
		line-height: 20px;
		text-transform: uppercase;
		font-weight: 400;
		padding-top: 12px;
		margin-top: -2px;
		max-width: 450px;

		@include mobile {
			font-size: 10px;
		}

		@include tablet {
			padding-top: 25px;
			padding-bottom: 30px;
			display: flex;
			justify-content: space-between;
			max-width: var(--container-max-width);
		}

		div:not(.social-links) {
			padding-bottom: 19px;

			@media (max-width: 360px) {
				text-align: center;
			}
		}

		div.footer_social-links {
			padding-bottom: 15px;
		}
	}

	&__footnote-links {

		a {
			margin-left: 5px;

			@include tablet {
				margin-left: 20px;
			}

			&:after {
				content: '';
				display: inline-block;
				margin-left: 5px;

				@include tablet {
					display: none;
				}
			}

			&:first-child {
				margin-left: 0;
			}

			&:last-child {

				&::after {
					display: none;
				}
			}
		}
	}

	&__copyright {
		margin-right: 20px;
	}

	@include tablet {    
		&__vertical-bar{
			display: none;
		}
	}

	.tooltip {
		display: none;

		ul {
			display: flex;
			padding-left: 0px;
			justify-content: space-around;
			margin: 0px;
		}

		.social-links {
			margin: 0px;
		}

		.social-links__title {
			display: none;
		}

		.icon {
			fill: var(--color-text);
		}

		.social-links__item {
			margin-bottom: 0;

			a {
				color: var(--color-text);
			}
		}

		.tooltip__title-link {
			@include tablet {    
				margin-right: 0px;
			}
		}
    }
}

.tooltip-open #shopify-section-footer .footer__footnote-links {
	display: none;
}

@include mobile {
	.template-search .footer,
	.template-collection .footer {
		padding-bottom: 60px;
	}
}

