//
//  Banner Overlay
//  _____________________________________________

@use '../utils/_media-queries' as *;

.banner-overlay {
  position: relative;
  margin-bottom: 6.24em;

  @include mobile {
    margin-bottom: 5.4em;
  }

  &__container {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0;
  }

  &__position-top {
    align-items: flex-start;
  }

  &__position-middle {
    align-items: center;
  }

  &__position-bottom {
    align-items: flex-end;

    .banner-overlay__text-container {
      @include desktop {
        margin: 30px 60px;
      }
    }
  }

  &__align-left {
    justify-content: flex-start;
  }

  &__align-right {
    justify-content: flex-end;
  }

  &__align-center {
    justify-content: center;
    text-align: center;
  }

  &__text-container {
    margin: 20px;

    @include tablet {
      max-width: 40%;
    }

    @include desktop {
      margin: 30px 60px 38px;
    }
  }

  &__title {
    margin: 0;
  }

  &__subtitle {
    margin-bottom: 0;
  }

  &__button {
    margin-top: 24px;

    @include mobile {
      margin-top: 37px;
    }
  }

  &__small {
    h1 {
      letter-spacing: 0;
      font-size: 25px;
      line-height: 16px;
    }

    p {
      font-size: 14px;
      line-height: 14px;
    }

    button {
      margin-top: 24px;
    }
  }

  &__link.btn-link:hover {
    text-decoration: none;
  }

  &__link.btn-link:active,
  &__link.btn-link:focus {
    color: #767676;
  }

  // Mobile - disable overlay
  &.mobile-disable-overlay {

    @include mobile {

      .banner-overlay__image {
        padding: 18px;
      }

      .banner-overlay__container {
        position: relative;
      }

      .banner-overlay__link,
      .banner-overlay__text-container {
        color: var(--color-text) !important;
      }

    }
    
  }

}


