//
//  Social tooltip
//  _____________________________________________

@use '../utils/_media-queries' as *;

.tooltip {
    position: relative;

    @include tablet {
        display: flex;
        justify-content: center;
    }

    &.align-right {
        justify-content: flex-end;

        .tooltip__title-link {
            @include tablet {
                margin-right: 20px;
            }
        }
    }

    &.align-left {
        justify-content: flex-start;
    }

    &__title-link {
        text-decoration: underline !important;
        font-size: 14px;
        line-height: 20px;
    }

    &__items {
        background-color: white;
        padding: 12px 10px 10px 10px;
        display: none;
        position: absolute;
        z-index: 5000;
        margin-top: 30px;

        &::before {
            content: ' ';
            height: 0;
            position: absolute;
            width: 0;
            top: -22px;
            transform: rotate(90deg);
            border: 12px solid transparent;
            border-right-color: white;
        }
        
        &.active {
            display: block;
        }

        &.align-right {
            right: 20px;

            &::before {
                right: 15%;
            }
        }

        &.align-left {
            left: 0;

            &::before {
                left: 15%;
            }
        }

        &:not(.align-left):not(.align-right) {
            &::before {
                right: calc(50% - 12px);
            }
        }
    }

    &__overlay {
        &.active {
            background: transparent;
        }
    }
}

