@use '../utils/_media-queries' as *;

//
//  Swiper slide
//  _____________________________________________

.swiper {

    .swiper-button-next,
    .swiper-button-prev {

        &.transparent-button {
            background: transparent !important;
        }

        @include desktop-max {
            display: none;            
        }
    }

    .swiper-pagination {

        @include desktop {
            display: none;
        }
    }

    &.swiper__product-card {
        padding-bottom: 50px;

        @include desktop {
            padding-bottom: 30px;
        }

        .swiper-pagination {
            bottom: 0;
            text-align: left;
        }

        quick-variant-selector ul {
            top: auto;
            bottom: 25px;
        }
    }
}