//
//  Modal
//  _____________________________________________

@use '../utils/_media-queries' as *;

body {
    &.modal-active {
        overflow: hidden;
    }
}

.modal {

    &__container {
        display: none;
        align-items: center;
        justify-content: center;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1000;
        background-color: var(--color-overlay);
        opacity: 0;
        visibility: hidden;
        will-change: visibility, opacity;
        transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1);

        &.active {
            display: flex;
            visibility: visible;
            opacity: 1;
        }
    }

    &__content {
        background-color: var(--color-white);
        padding: 50px 25px;
        overflow-y: auto;
        overflow-x: hidden;
        position: relative;
        max-height: 90%;

        @include tablet {
            padding: 55px 70px 55px 60px;            
        }

        &--full-screen {
            max-height: 100%;
            width: 100%;
            height: 100%;
        }

        &--large {
            max-width: 90%;

            @include tablet {
                max-width: 80%;           
            }

            @include desktop {
                max-width: 60%;           
            }
        }

        &--default {
            max-width: 90%;

            @include desktop {
                max-width: 60%;           
            }

            @include desktop-large {
                max-width: 40%;           
            }
        }

        &--small {
            max-width: 90%;
            padding: 30px 45px 30px 35px;

            @include tablet {
                max-width: 60%;
                padding: 35px 60px 35px 50px;           
            }

            @include desktop {
                max-width: 30%;           
            }

            @include desktop-large {
                max-width: 25%;          
            }
        }

        &::-webkit-scrollbar {
            width: 8px;
        }

        &::-webkit-scrollbar-thumb {
            background: #DADADA;
            border: 2px solid #FFFFFF;
            border-radius: 2px;
        }

        &::-webkit-scrollbar-thumb:hover {
            background: #767676;
        }

        img {
            width: 100%;
            max-width: 350px;
        }
    }

    &__close {
        background: transparent;
        position: absolute;
        top: 0;
        right: 0;
        padding: 21px 17px;

        .icon-close {
            fill: #767676;
        }
    }
}

    

