//
//  Account page
//  _____________________________________________

@use '../utils/_media-queries' as *;

.account-template {
    text-align: center;
    margin: 35px 0 100px;

    &__header,
    &__orders,
    &__address {
        margin-bottom: 50px;
    }

    &__header {
        text-align: center;
    }

    &__title {
        margin-bottom: 8px;
    }

    &__address {
        p {
            margin: 0;
        }
    }

    &__view_addresses {
        margin-top: 8px;
        .btn {
            padding: 8px;
            font-size: 12px; 
        }
    }

    &__table-orders {
        width: 100%;
        border-spacing: 0;
        border-collapse: collapse;
         
        th, td {
            text-align: left;
            border: 1px solid var(--color-border);
            padding: 10px 14px;
        }
        
        @include mobile {
            thead {
                display: none;
            } 
    
            th,
            td {
                float: left;
                clear: left;
                width: 100%;
                text-align: right;
                padding: 10px 14px;
                border: 0;
                margin: 0;

                &:before {
                    content: attr(data-label);
                    float: left;
                    text-align: center;
                    font-size: 14px;
                    padding-right: 10px;
                    font-weight: bold;
                }
            }
    
            tr {
                &:before {
                    content: '';
                    position: relative;
                    width: 100%;
                    height: 1px;
                    background: var(--color-border);
                    bottom: 100%;
                    left: 0;
                    display: block;
                    margin: 12px 0;
                }
            }
    
            tr:first-of-type {
                &:before {
                    content: none;
                }
            }
        
        }

    }

}
