@use '../utils/_media-queries' as *;

.popup {

    background: var(--color-white);
    position: fixed;
    width: 298px;
    height: 98px;
    right: 16px;
    bottom: 80px;
    border: 1px solid rgba(218, 218, 218, 0.5);
    box-sizing: border-box;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
    padding: 8px 16px;
    z-index: 1;
    visibility: hidden;

    &.active {
        @include desktop {
            visibility: visible;
            opacity: 1;
        }
    }

    span {
        display: block;
    }

    &__close {
        position: absolute;
        top: 8px;
        right: 8px;
        text-decoration: none;
        font-size: 14px;
        color: #767676;
        cursor: pointer;
    }

    &__banner {
        font-weight: bold;
        font-size: 12px;
        line-height: 28px;
        text-transform: uppercase;
        color: #D22026;
    }

    &__title {
        font-size: 20px;
        line-height: 27px;
        color: var(--color-text);
    }

    &__description {
        font-size: 14px;
        line-height: 20px;
        color: #767676;
    }
}